import React from "react";

const DashboardLout = ({ children }) => {
  return (
    <>
      <div className="container">
        <div className={style.HomeContainer}>
          <div className={style.Home_left_container}></div>
        </div>
        <div className="Home_Middle_container"></div>
        <div className="Home_feed_container">{children}</div>
      </div>
    </>
  );
};

export default DashboardLout;
